<template>
    <v-layout column align-start>
        <v-layout row id="Structural-setup">
            <p>Structural setup</p>
        </v-layout>

        <v-layout row class="Rectangle">
            <v-layout justify-center align-center="center" column fill-height>
                <v-form ref="form" class="structural-form" v-model="valid">
                    <v-select
                        class="select-item"
                        v-model="tenantId"
                        :items="listOfTenants"
                        item-text="country"
                        item-value="tenantId"
                        label="Select Country"
                        v-on:change="selectCountry"
                        :rules="[v => !!v || 'Country is required']"
                    ></v-select>

                    <v-select
                        class="select-item"
                        v-model="buildingSetup.locationId"
                        :items="listOfCities"
                        item-text="name"
                        item-value="locationId"
                        label="Select City"
                        :rules="[v => !!v || 'City is required']"
                        :disabled="!this.tenantId"
                    ></v-select>

                    <v-text-field
                        class="text-building"
                        label="Building Name"
                        :rules="buildingNameRules"
                        :disabled="!this.buildingSetup.locationId"
                        v-model="buildingSetup.buildingName"
                    ></v-text-field>

                    <floorList
                        :addIsActive="buildingHasValue"
                        v-on:changeFloors="updateFloors($event)"
                    ></floorList>

                    <v-layout row>
                        <v-spacer></v-spacer>
                        <v-flex>
                            <v-btn
                                class="white--text"
                                flat
                                color="primary"
                                v-on:click="save"
                                :disabled="!valid"
                            >Save</v-btn>
                            <v-btn class="white--text" flat color="primary" to="buildings">Cancel</v-btn>
                        </v-flex>
                    </v-layout>
                </v-form>
            </v-layout>
        </v-layout>
    </v-layout>
</template>

<script>
import FloorList from '@/components/content/FloorList'
import { mapState, mapActions } from 'vuex'
import router from '@/router'
export default {
    name: 'BuildingSetupForm',
    components: {
        floorList: FloorList
    },
    data: () => ({
        valid: false,
        tenantId: '',
        buildingSetup: {
            buildingName: '',
            floors: [],
            locationId: ''
        },
        buildingNameRules: [v => !!v || 'Building name is required', v => (v && v.length >= 3) || 'Building name must be at least 3 characters long']
    }),
    computed: {
        ...mapState('buildingInformation', ['listOfTenants', 'listOfCities', 'building']),
        buildingHasValue() {
            return this.buildingSetup.buildingName.length < 3
        }
    },
    methods: {
        ...mapActions('buildingInformation', ['getTenants', 'getCities', 'postBuildingSetup']),
        selectCountry() {
            this.buildingSetup.locationId = ''
            this.getCities(this.tenantId)
        },
        updateFloors(newFloors) {
            this.buildingSetup.floors = newFloors
        },
        save() {
            if (this.$refs.form.validate()) {
                this.postBuildingSetup(this.buildingSetup).then(() => {
                    router.push({ name: 'buildings' })
                })
            }
        }
    },
    created() {
        this.getTenants()
    }
}
</script>

<style lang="scss" scoped>
@import '@/styles/global.scss';

#Structural-setup {
    width: 181px;
    height: 29px;

    font-family: Roboto;
    font-size: 25px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;

    color: #2b363b;
    align-self: center;
    margin-top: 50px;
}

.Rectangle {
    margin-top: 20px;
    padding: 20px 0;

    position: relative;
    align-self: center;

    width: 559px;
    height: 100%;
    background-color: #f3f3f3;
}

.structural-form {
    margin-top: -40px;
}

.select-item {
    margin-top: 10%;
    width: 430px;
    height: 29px;
}
.text-building {
    width: 430px;
    height: 28px;
    margin-top: 10%;
}

.btn-save-Rectangle {
    @include red-button;
    width: 435px;
    height: 40px;
    margin-left: -3px;
    margin-top: 20px;
    margin-bottom: 20px;
}
</style>
