<template>
    <v-layout row wrap align-start justify-start>
        <v-flex xs12 md6 lg4 xl2>
            <TileContainer color="white">
                <v-layout column align-space-around align-center justify-center fill-height>
                    <v-flex xs2>
                        <v-btn fab to="create-building" outline class="Oval" color="#db4326">
                            <v-icon dark>add</v-icon>
                        </v-btn>
                    </v-flex>
                    <v-flex xs1>Add Building</v-flex>
                </v-layout>
            </TileContainer>
        </v-flex>
        <template v-for="building in listOfBuildings">
            <v-flex xs12 md6 lg4 xl2 :key="`tile${building.buildingId}`">
                <TileContainer color>
                    <v-layout column align-space-around align-center justify-center fill-height>
                        <v-flex xs2>{{building.location}}</v-flex>
                        <v-flex xs1>{{ building.name }}</v-flex>
                    </v-layout>
                </TileContainer>
            </v-flex>
        </template>
        <v-spacer></v-spacer>
    </v-layout>
</template>

<script>
import TileContainer from '@/components/shared/TileContainer'
import { mapGetters, mapActions } from 'vuex'

export default {
    components: {
        TileContainer
    },
    computed: {
        ...mapGetters('buildings', ['listOfBuildings'])
    },
    mounted() {
        this.getBuildings()
    },
    methods: {
        ...mapActions('buildings', ['getBuildings'])
    }
}
</script>

