
<template>
    <v-layout column>
        <v-layout row class="add_floors">
            <v-flex align-content-start>
                <p>Floors:</p>
            </v-flex>

            <v-flex align-content-end>
                <v-btn
                    class="white--text btn-add-Rectangle"
                    v-on:click="dialog = true"
                    v-bind:disabled="addIsActive"
                >Add</v-btn>

                <v-dialog v-model="dialog" persistent max-width="600px">
                    <v-layout>
                        <v-card>
                            <v-form ref="form" class="floor-form" v-model="valid">
                                <v-card-title>
                                    <span
                                        class="headline"
                                    >{{this.index === -1 ? 'New Floor' : 'Edit Floor'}}</span>
                                </v-card-title>

                                <v-card-text>
                                    <v-layout wrap>
                                        <v-flex xs12>
                                            <v-text-field
                                                label="Name"
                                                :rules="[rules.required]"
                                                v-model="editedItem.name"
                                            ></v-text-field>
                                        </v-flex>

                                        <v-flex xs12>
                                            <v-text-field
                                                label="Level"
                                                :rules="[rules.required, rules.number]"
                                                v-model="editedItem.level"
                                            ></v-text-field>
                                        </v-flex>

                                        <v-flex xs12>
                                            <v-select
                                                :items="floor_types"
                                                item-text="type.name"
                                                item-value="type.id"
                                                label="FloorType"
                                                :rules="[rules.required]"
                                                v-model="editedItem.type"
                                            ></v-select>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>

                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="blue darken-1" flat v-on:click="cancel">Close</v-btn>

                                    <v-btn
                                        color="blue darken-1"
                                        flat
                                        v-on:click="add"
                                        :disabled="!valid"
                                    >{{this.index === -1 ? 'Add' : 'Edit'}}</v-btn>
                                </v-card-actions>
                            </v-form>
                        </v-card>
                    </v-layout>
                </v-dialog>
            </v-flex>
        </v-layout>

        <v-layout column>
            <v-flex v-for="item in floors" :key="item.level">
                <v-layout row class="floor">
                    <v-flex justify-self-start xs6>{{item.name}}</v-flex>

                    <v-flex xs2>Floor: {{item.level}}</v-flex>

                    <v-flex xs4>Type: {{floor_types[item.type-1].type.name}}</v-flex>

                    <v-flex justify-self-end xs1>
                        <v-layout row>
                            <v-icon small class="mr-2" @click="editItem(item)">edit</v-icon>

                            <v-icon small @click="deleteItem(item)">delete</v-icon>
                        </v-layout>
                    </v-flex>
                </v-layout>
            </v-flex>
        </v-layout>
    </v-layout>
</template>

<script>
export default {
    name: 'FloorList',
    props: {
        addIsActive: {
            type: Boolean,
            required: true,
            default: false
        }
    },
    components: {},
    data: () => ({
        rules: {
            required: value => !!value || 'Required.',
            number: value => !isNaN(value) || 'Input must be a number'
        },

        dialog: false,
        valid: false,

        floor_types: [
            { type: { name: 'Office', id: 1 } },
            { type: { name: 'Office with Reception', id: 2 } },
            { type: { name: 'Parking', id: 3 } },
            { type: { name: 'Other', id: 10 } }
        ],

        floors: [],

        index: -1,
        editedItem: {},
        defaultItem: {}
    }),
    watch: {
        floors(newFloors) {
            this.$emit('changeFloors', newFloors)
        }
    },
    methods: {
        paramReset() {
            this.index = -1
            this.editedItem = {
                name: null,
                type: null,
                level: null
            }
        },
        editItem(item) {
            this.index = this.floors.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true
        },
        deleteItem(item) {
            const index = this.floors.indexOf(item)
            confirm('Are you sure you want to delete this item?') && this.floors.splice(index, 1)
            this.editedItem = Object.assign({}, this.defaultItem)
            this.index = -1
        },
        cancel() {
            this.dialog = false
            setTimeout(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.index = -1
            }, 300)
        },
        add() {
            if (this.index > -1) {
                Object.assign(this.floors[this.index], this.editedItem)
            } else {
                this.floors.push({
                    name: this.editedItem.name,
                    type: this.editedItem.type,
                    level: Number.parseInt(this.editedItem.level)
                })
            }
            this.dialog = false
            this.paramReset()
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/styles/global.scss';

.btn-add-Rectangle {
    @include red-button;
    width: 441px;
    height: 40px;
    border-radius: 4px;
    margin-top: 39px;
    margin-left: 25px;
}
.text-floor {
    width: 290px;
    height: 28px;
    margin-top: 10%;
}
.select-item {
    margin-top: 10%;
    width: 290px;
    height: 29px;
}
.small-rectangle {
    margin-top: 20px;
    width: 559px;
    height: 100%;
    background-color: #f3f3f3;
    column-fill: height;
    position: relative;
    align-self: center;
}
.floor-form {
    padding: 20px;
}
.add_floors {
    margin-top: 50px;
    font-size: 1.15em;
}
.btn-add-Rectangle {
    width: 40px;
    height: 30px;
    float: right;
    margin-top: -5px;
}
.floor {
    border: 0.5px solid lightgray;
    border-radius: 4px;
    padding: 1px 10px;
    margin-bottom: 7px;
    margin-left: -9px;
}
</style>
